import React, { useState } from 'react';
import axios from 'axios';
import { Collapse, Button, CardBody, CardSubtitle, Card, FormGroup, Label, Input } from 'reactstrap';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const Dadd = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const [newDepartment, setNewDepartment] = useState('');
  const [alertMessage, setAlertMessage] = useState("")

  const token = 'YWRtaW5hZG1pbjEyMw==';
  const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));
  const onChange = (event) => {
    setNewDepartment(event.target.value);
  };
const departmentName = newDepartment;

  const config = {
      headers: { Authorization: `Bearer ${token}`,
        'content-type': 'application/x-www-form-urlencoded',
      },

      // headers: { 'content-type': 'application/x-www-form-urlencoded' }
    };

  const data = {
    department: departmentName,
  };

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  const onExiting = () => {
    console.log(data);
    axios.post(
      'https://businessbook.co.in/api/api/department/',
      data,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response);
      setAlertMessage(response.data.response.message);
      handleToggle();

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(departmentName);
  };

  const classes = useStyles();
    const [open, setOpen] = useState(false);
    const handleClose = () => {
      setOpen(false);
    };
    const handleToggle = () => {
      setOpen(!open);
    };

  return (
    <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column' }}>
      <Button color="success" onClick={toggle} style={{ marginBottom: '1rem', width: '400px' }}>Add</Button>
        <Collapse
        isOpen={isOpen}
        onExiting={onExiting}
        >
        <Card style={{width: '400px', marginBottom: '20px'}}>
          <CardBody>
            <FormGroup>
              <Label for="exampleSelect">Enter Department</Label>
              <Input type="text" name="Department" value={newDepartment} onChange={onChange} required/>
            </FormGroup>

          </CardBody>
        </Card>
      </Collapse>

      <Backdrop className={classes.backdrop} open={open}>
        <Card className='bcar'>
        <IconContext.Provider value={{ color: "#2dce89", size: '10rem' }}>
          <div className='bcar-body'><FiCheckCircle /></div>
        </IconContext.Provider>
          <CardSubtitle tag="h2" className='bcar-sub'>{alertMessage}</CardSubtitle>
          <CardBody  className='bcar-body'>
            <Button className='btn-danger'  onClick={handleClose} href='/admin/Department'>Done</Button>
          </CardBody>
        </Card>
      </Backdrop>
    </div>
  );
}

export default Dadd;
