import React, { useEffect, useState } from 'react';
import axios from 'axios';
import history from '../../history.js';
import { Col, Row, Button, Form, FormGroup, Label, Input } from 'reactstrap';

const CustomerEdit = (props) => {

    let CustID = sessionStorage.getItem('custID')
    CustID = JSON.parse(CustID)

    let Client = sessionStorage.getItem('client')
    Client = JSON.parse(Client)

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` },

  };

  const bodyParameters = {
     key: "value"
  };

  const [values, setValues] = useState({
    id: CustID, name: Client.name, mobile: Client.mobile, longitude: Client.longitude, latitude: Client.latitude, password: Client.password
  });
  const click = () => {
    console.log(values);
  }
  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const updateItem = () => {
    axios.put(
      'https://businessbook.co.in/api/api/clientUser/',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response);
      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
    props.history.push('/admin/Customers')
  }

  return (
    <Form style={{margin: '70px'}}>
      <FormGroup>
        <Label for="exampleID">ID</Label>
        <Input type="text" name="id" id="exampleID" value={Client.id} disabled />
      </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleName">Name</Label>
              <Input type="text" name="name" id="exampleName" defaultValue={Client.name} onChange={set('name')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="examplePassword">Password</Label>
              <Input type="password" name="password" id="examplePassword" defaultValue={Client.password} onChange={set('password')} />
            </FormGroup>
          </Col>
        </Row>
        <FormGroup>
          <Label for="exampleNumber">Mobile Number</Label>
          <Input type="tel" name="number" id="exampleNumber" defaultValue={Client.mobile} onChange={set('mobile')} />
        </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleLatitude">Latitude</Label>
              <Input type="text" name="latitude" id="exampleLatitude" defaultValue={Client.latitude} onChange={set('latitude')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleLongitude">Longitude</Label>
              <Input type="text" name="longitude" id="exampleLongitude" defaultValue={Client.longitude} onChange={set('longitude')} />
            </FormGroup>
          </Col>
        </Row>
      <Button className='btn-success' onClick={() => updateItem()}>Update</Button>
    </Form>
  );
}

export default CustomerEdit;
