import React, { useEffect, useState } from 'react';
import axios from 'axios';
import history from '../../history.js';
import { Col, Row, Button, Form, FormGroup, Label, Input, Card, CardBody, CardSubtitle } from 'reactstrap';

import Header from '../Headers/Header.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const ProviderEdit = (props) => {
  const [values, setValues] = useState('');
  const [alertMessage, setAlertMessage] = useState("")
  const [open, setOpen] = useState(false);
  const classes = useStyles();
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };

    let ProvID = sessionStorage.getItem('provID')
    ProvID = JSON.parse(ProvID)

    let NProv = sessionStorage.getItem('provider')
    NProv = JSON.parse(NProv)

  const [users, setUsers] = useState([])

  window.onload = function() {
    if(!window.location.hash) {
      window.location = window.location + '#loaded';
      window.location.reload();
    }
  }

useEffect(() => {
        if(!getData())
        {
          getData();
        }
}, [])

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` },

  };

  const bodyParameters = {
     key: "value"
  };

  const data = {
     id: ProvID
  };

  const getData = () =>{

    axios.post(
      'https://businessbook.co.in/api/api/user/single_read.php',
      data,
      config,
    ).then((response)=> {
      setUsers(response.data.response.user)
      sessionStorage.setItem('provider', JSON.stringify(response.data.response.user));
      console.log(NProv);
      setValues({
        id: ProvID, name: NProv.name, email: NProv.email, dob: NProv.dob, address1: NProv.address1, address2: NProv.address2, state: NProv.state, city: NProv.city, zip: NProv.zip, department: NProv.department, type: NProv.type, gov_regi: NProv.gov_regi, working_hour: NProv.working_hour, starttime: NProv.starttime, endtime: NProv.endtime,
      });
    }).catch();
  }

  const User = users;

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const UpdateItem = () => {

    axios.put(
      'https://businessbook.co.in/api/api/user/',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response);
      setAlertMessage(response.data.response.message);
      handleToggle();

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
  }

  return (
    <div>
      <Header />

    <Form style={{margin: '70px'}}>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleID">ID</Label>
              <Input type="text" name="id" id="exampleID" value={User.id} disabled />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleName">Name</Label>
              <Input type="text" name="name" id="exampleName" defaultValue={User.name} onChange={set('name')} />
            </FormGroup>
          </Col>
        </Row>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleEmail">Email</Label>
              <Input type="email" name="email" id="exampleEmail" defaultValue={User.email} onChange={set('email')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleDob">DOB</Label>
              <Input type="date" name="dob" id="exampleDob" defaultValue={User.dob} onChange={set('dob')} />
            </FormGroup>
          </Col>
        </Row>
        <FormGroup>
          <Label for="exampleAdd1">Address1</Label>
          <Input type="text" name="address1" id="exampleAdd1" defaultValue={User.address1} onChange={set('address1')} />
        </FormGroup>
        <FormGroup>
          <Label for="exampleAdd2">Address2</Label>
          <Input type="text" name="address2" id="exampleAdd2" defaultValue={User.address2} onChange={set('address2')} />
        </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleState">State</Label>
              <Input type="text" name="state" id="exampleState" defaultValue={User.state} onChange={set('state')} />
            </FormGroup>
          </Col>
          <Col md={4}>
            <FormGroup>
              <Label for="exampleCity">City</Label>
              <Input type="text" name="city" id="exampleCity" defaultValue={User.city} onChange={set('city')} />
            </FormGroup>
          </Col>
          <Col md={2}>
            <FormGroup>
              <Label for="exampleZip">Zip</Label>
              <Input type="text" name="zip" id="exampleZip" defaultValue={User.zip} onChange={set('zip')} />
            </FormGroup>
          </Col>
        </Row>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleDepartment">Department</Label>
              <Input type="text" name="department" id="exampleDepartment" defaultValue={User.department} onChange={set('department')} />
            </FormGroup>
          </Col>
          <Col md={4}>
            <FormGroup>
              <Label for="exampleType">Type</Label>
              <Input type="text" name="type" id="exampleType" defaultValue={User.type} onChange={set('type')} />
            </FormGroup>
          </Col>
          <Col md={2}>
            <FormGroup>
              <Label for="exampleRegi">Registration</Label>
              <Input type="text" name="gov_regi" id="exampleRegi" defaultValue={User.gov_regi} onChange={set('gov_regi')} />
            </FormGroup>
          </Col>
        </Row>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleWorking">Working Hours</Label>
              <Input type="text" name="working_hour" id="exampleWorking" defaultValue={User.working_hour} onChange={set('working_hour')} />
            </FormGroup>
          </Col>
          <Col md={4}>
            <FormGroup>
              <Label for="exampleStart">Start</Label>
              <Input type="text" name="starttime" id="exampleStart" defaultValue={User.starttime} onChange={set('starttime')} />
            </FormGroup>
          </Col>
          <Col md={2}>
            <FormGroup>
              <Label for="exampleEnd">End</Label>
              <Input type="text" name="endtime" id="exampleEnd" defaultValue={User.endtime} onChange={set('endtime')} />
            </FormGroup>
          </Col>
        </Row>
      <Button className='btn-warning bcentr' onClick={() => UpdateItem()}>Update</Button>
    </Form>

    <Backdrop className={classes.backdrop} open={open}>
      <Card className='bcar'>
      <IconContext.Provider value={{ color: "#fb6340", size: '10rem' }}>
        <div className='bcar-body'><FiCheckCircle /></div>
      </IconContext.Provider>
        <CardSubtitle tag="h2" className='bcar-usub'>{alertMessage}</CardSubtitle>
        <CardBody  className='bcar-body'>
          <Button className='btn-warning'  onClick={handleClose} href='/admin/Providers'>Done</Button>
        </CardBody>
      </Card>
    </Backdrop>
  </div>
  );
}

export default ProviderEdit;
