import React, { useEffect, useState, useCallback } from 'react';
import axios from 'axios';
import history from '../../history.js';

import { CopyToClipboard } from 'react-copy-to-clipboard';
// reactstrap components
import {
  Badge,
  Button,
  Card,
  CardHeader,
  CardFooter,
  DropdownMenu,
  DropdownItem,
  Input,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  Container,
  Row,
  UncontrolledTooltip
} from 'reactstrap';

import Header from '../../components/Headers/Header.js';

require('es6-promise').polyfill();
require('isomorphic-fetch')
// core components

const URL = 'https://businessbook.co.in/api/api/user/read.php'
const token = 'YWRtaW5hZG1pbjEyMw==';

// const token = JSON.parse(sessionStorage.getItem('YWRtaW5hZG1pbjEyMw=='));

function Customers(props){

  const [users, setUsers] = useState([])
  const [q, setQ]  = useState('')

  useEffect(() => {
      getData()
  }, [])

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const bodyParameters = {
     key: "value"
  };

const getData = () =>{
  axios.post(
    'https://businessbook.co.in/api/api/clientUser/read.php',
    bodyParameters,
    config
  ).then((response)=> {
    setUsers(response.data.response.clientUsers)
    console.log(response.data.response.clientUsers)
  }).catch();
}

//   const getData = async () => {
//
//       const response = await axios.get(URL, { headers: { Authorization: "Bearer " + token,
//   Accept: "application/json, text/plain, */*",
//   "Content-Type": "application/json"
// } })
//       setUsers(response.data)
//       console.log(response.data)
//   }

const deleteItem = (id, e) => {

  // axios.post("https://businessbook.co.in/api/api/user/", id)
  axios.delete(`http://localhost/dashboard/businessbook.co.in/api/user/`, {
  headers: {
    Authorization: `Bearer ${token}`
    },
    data: {
      id: id
    }
  }).then((response)=> {
        //   const sData = this.state.sData.filter(item => item.id !== id);
        // this.setState({ sData });
      console.log(response)
      getData();
      }).catch();
    }



  const editItem = (id) => {
    const data = {
       id: id
    };
    axios.post(
      'https://businessbook.co.in/api/api/clientUser/single_read.php',
      data,
      config,
    ).then((response)=> {
      console.log(response.data.response.clientUsers)
      sessionStorage.setItem('client', JSON.stringify(response.data.response.clientUsers));
    }).catch();
    sessionStorage.setItem('custID', JSON.stringify(id));
    props.history.push('/CustomerEdit')
  }

function search(rows)
{
  return rows.filter((row) =>
    row.name.toLowerCase().indexOf(q) > -1 ||
    row.city.toLowerCase().indexOf(q) > -1 ||
    row.department.toLowerCase().indexOf(q) > -1 ||
    row.email.toLowerCase().indexOf(q) > -1
    );
}

const sData = search(users)
const columns = sData[0] && Object.keys(sData[0])



    return (
      <>
        <Header />
        {/* Page content */}
        <Container className='mt--7' fluid>


          {/* Table */}
          <Row>
            <div className='col'>
              <Card className='shadow'>
                <CardHeader className='border-0'>
                  <Input type='text' value={q} onChange={(e) => setQ(e.target.value)}/>
                </CardHeader>
                <Table className='align-items-center table-flush' responsive>
                	<thead className='thead-light'>
                	<tr>
                    <th scope='col'>Id</th>
                    <th scope='col'>Name</th>
                    <th scope='col'>Mobile Number</th>
                    <th scope='col'>Longitude</th>
                    <th scope='col'>Latitude</th>
                    <th scope='col'>Timestamp</th>
                    <th scope='col'>Action</th>
                  </tr>

                	</thead>
                  <tbody>
                    {sData.map(row =>
                        <tr>
                            <td>{row.id}</td>
                            <td>{row.name}</td>
                            <td>{row.mobile}</td>
                            <td>{row.longitude}</td>
                            <td>{row.latitude}</td>
                            <td>{row.timestamp}</td>
                            <td>
                              <Button className='btn-danger' onClick={(e) => deleteItem(row.id)}>Delete</Button>
                              <Button className="btn-warning" onClick={() => editItem(row.id)}>Edit</Button>
                            </td>
                        </tr>
                  )}
                  </tbody>

                </Table>
                <CardFooter className='py-4'>
                  <nav aria-label='...'>
                    <Pagination
                      className='pagination justify-content-end mb-0'
                      listClassName='justify-content-end mb-0'
                    >
                      <PaginationItem className='disabled'>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                          tabIndex='-1'
                        >
                          <i className='fas fa-angle-left' />
                          <span className='sr-only'>Previous</span>
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem className='active'>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          1
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          2 <span className='sr-only'>(current)</span>
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          3
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          <i className='fas fa-angle-right' />
                          <span className='sr-only'>Next</span>
                        </PaginationLink>
                      </PaginationItem>
                    </Pagination>
                  </nav>
                </CardFooter>
              </Card>
            </div>
          </Row>
        </Container>
      </>
    );
  }


export default Customers;



