import React, { useState, useEffect } from 'react';
import axios from 'axios';
import history from '../../../history.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardHeader,
  CardFooter,
  DropdownMenu,
  DropdownItem,
  Input,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  Container,
  Row,
  UncontrolledTooltip
} from 'reactstrap';

import CTadd from './CTadd.js'

import Header from '../../Headers/Header.js';
require('es6-promise').polyfill();
require('isomorphic-fetch')

const token = 'YWRtaW5hZG1pbjEyMw==';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const City = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const [states, setStates] = useState([])
  const [q, setQ]  = useState('')
  const [isEditing, setIsEditing] = useState(false);
  const [alertMessage, setAlertMessage] = useState("")

  useEffect(() => {
      getData()
  }, [])

  const config = {
    key: "value",
    headers: { Authorization: `Bearer ${token}` },

  };

  const bodyParameters = {
  };

const getData = () =>{
  axios.post(
    'https://businessbook.co.in/api/api/state/getAllState.php',
    bodyParameters,
    config
  ).then((response)=> {
    setStates(response.data.response.state)
    console.log(response.data.response.state)
  }).catch();
}

//   const getData = async () => {
//
//       const response = await axios.get(URL, { headers: { Authorization: "Bearer " + token,
//   Accept: "application/json, text/plain, */*",
//   "Content-Type": "application/json"
// } })
//       setStates(response.data)
//       console.log(response.data)
//   }

const deleteItem = (id, e) => {

  // axios.post("https://businessbook.co.in/api/api/user/", id)
  axios.delete(`https://businessbook.co.in/api/api/state/`, {
  headers: {
    Authorization: `Bearer ${token}`
    },
    data: {
      state_id: id
    }
  }).then((response)=> {
        //   const sData = this.state.sData.filter(item => item.id !== id);
        // this.setState({ sData });
        console.log(response.data.response.message);
        setAlertMessage(response.data.response.message);
        dhandleToggle();
        getData();
      }).catch();
    }



  const editItem = (id) => {
    setIsEditing(true);
    const data = {
      dist_id: id,
    }
    axios.post(
      'https://businessbook.co.in/api/api/city/getAllCity.php',
      data,
      config
    ).then((response)=> {
      setStates(response.data.response.city)
      console.log(response.data.response.city)
    }).catch();
  }

  const updateItem = (stateid, distid, cname) => {
    setIsEditing(false);
    let cityData = { state_id: stateid, dist_id: distid, name: cname }
    sessionStorage.setItem('cityData', JSON.stringify(cityData));
    props.history.push('/admin/CtUpdate')
  }

function search(rows)
{
  return rows.filter((row) =>
    row.state_id.toLowerCase().indexOf(q) > -1 ||
    row.dist_id.toLowerCase().indexOf(q) > -1 ||
    row.name.toLowerCase().indexOf(q) > -1
    );
}

const sData = search(states)
const columns = sData[0] && Object.keys(sData[0])

  const toggle = () => setIsOpen(!isOpen);

  const Style = { minWidth: "190px" };
  const classes = useStyles();
    const [open, setOpen] = useState(false);
    const handleClose = () => {
      setOpen(false);
    };
    const dhandleToggle = () => {
      setOpen(!open);
    };

  return (
    <>
      <Header />

        <div>
          <Row>
            <div className='col'>
              <Card className='shadow'>
                <CardHeader className='border-0'>
                  <Input type='text' value={q} onChange={(e) => setQ(e.target.value)}/>
                  <div style={{display: 'flex', justifyContent: 'center', marginTop: '15px'}}><CTadd /></div>
                </CardHeader>
                <Table className='align-items-center table-flush' responsive >
                  <thead className='thead-light'>
                  <tr>
                    <th style={Style} scope='col'>State ID</th>
                    <th style={Style} scope='col'>Dist ID</th>
                    { isEditing ? (
                      <th style={Style} scope='col'>City Name</th>
                      ) : (
                        <th style={Style} scope='col'>State Name</th>
                    )}
                    <th style={Style} scope='col'>Timestamp</th>
                    <th style={Style} scope='col'>Action</th>
                  </tr>

                  </thead>
                  <tbody>
                    {sData.map(row =>
                        <tr>
                            <td>{row.state_id}</td>
                            <td>{row.dist_id}</td>
                            <td>{row.name}</td>
                            <td>{row.timestamp}</td>
                            <td>
                              <Button className='btn-danger' onClick={(e) => deleteItem(row.state_id)}>Delete</Button>
                              { isEditing ? (
                                <Button className="btn-warning" onClick={() => updateItem(row.state_id, row.dist_id, row.name)}>Update</Button>
                              ) : (
                                <Button className="btn-warning" onClick={() => editItem(row.state_id)}>Edit</Button>
                              )}
                            </td>
                        </tr>
                  )}
                  </tbody>

                </Table>
                <CardFooter className='py-4'>
                  <nav aria-label='...'>
                    <Pagination
                      className='pagination justify-content-end mb-0'
                      listClassName='justify-content-end mb-0'
                    >
                      <PaginationItem className='disabled'>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                          tabIndex='-1'
                        >
                          <i className='fas fa-angle-left' />
                          <span className='sr-only'>Previous</span>
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem className='active'>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          1
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          2 <span className='sr-only'>(current)</span>
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          3
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          <i className='fas fa-angle-right' />
                          <span className='sr-only'>Next</span>
                        </PaginationLink>
                      </PaginationItem>
                    </Pagination>
                  </nav>
                </CardFooter>
              </Card>
            </div>
          </Row>

          <Backdrop className={classes.backdrop} open={open}>
            <Card className='bcar'>
            <IconContext.Provider value={{ color: "#f5365c", size: '10rem' }}>
              <div className='bcar-body'><FiCheckCircle /></div>
            </IconContext.Provider>
              <CardSubtitle tag="h2" className='bcar-dsub'>{alertMessage}</CardSubtitle>
              <CardBody  className='bcar-body'>
                <Button className='btn-danger'  onClick={handleClose}>Done</Button>
              </CardBody>
            </Card>
          </Backdrop>
      </div>

    </>
  );
}

export default City;
