import React, { useEffect, useState, useCallback } from 'react';
import axios from 'axios'
import history from '../../history.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

import { CopyToClipboard } from 'react-copy-to-clipboard';
// reactstrap components
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardImg,
  CardTitle,
  CardSubtitle,
  Label,
  FormGroup,
  Collapse,
  CardHeader,
  CardFooter,
  DropdownMenu,
  DropdownItem,
  Input,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  Container,
  Row,
  Col,
  UncontrolledTooltip
} from 'reactstrap';

import Header from '../../components/Headers/Header.js';

require('es6-promise').polyfill();
require('isomorphic-fetch')
// core components

const token = 'YWRtaW5hZG1pbjEyMw==';
const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

// const token = JSON.parse(sessionStorage.getItem('YWRtaW5hZG1pbjEyMw=='));

function Employee(props){
  const [isOpen, setIsOpen] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [users, setUsers] = useState([]);
  const [q, setQ]  = useState('');
  const [values, setValues] = useState({
    id: '', name: '', email: '', age: '', designation:'', password: ''
  });

  if(sessionStorage.user === undefined) {
        console.log('chalo');
        props.history.push('/auth/login')
        window.location.reload(false);
      }

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  useEffect(() => {
      getData()
  }, [])

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const bodyParameters = {
     key: "value"
  };

const getData = () =>{
  axios.post(
    'http://businessbook.co.in/api/api/employees/read.php',
    bodyParameters,
    config
  ).then((response)=> {
    setUsers(response.data.response.employees)
    console.log(response.data.response.employees)
  }).catch();
}

//   const getData = async () => {
//
//       const response = await axios.get(URL, { headers: { Authorization: "Bearer " + token,
//   Accept: "application/json, text/plain, */*",
//   "Content-Type": "application/json"
// } })
//       setUsers(response.data)
//       console.log(response.data)
//   }

const deleteItem = (id, e) => {

  // axios.post("https://businessbook.co.in/api/api/user/", id)
  axios.delete(`https://businessbook.co.in/api/api/employees/`, {
  headers: {
    Authorization: `Bearer ${token}`
    },
    data: {
      id: id
    }
  }).then((response)=> {
        //   const sData = this.state.sData.filter(item => item.id !== id);
        // this.setState({ sData });
      console.log(response)
      setAlertMessage(response.data.response.message);
      dhandleToggle();
      getData();
      }).catch();
    }

    const editItem = (id) => {
      sessionStorage.setItem('empID', JSON.stringify(id));
      props.history.push('/admin/EmployeeEdit')
    }

    const toggle = () => setIsOpen(!isOpen);

    const onExiting = () => {

      axios.post(
        'https://businessbook.co.in/api/api/employees/',
        values,
        config,
      ).then((response)=> {
        // setUsers(response.data.response.users)
        console.log(response);
        setAlertMessage(response.data.response.message);
        ahandleToggle();
        getData();
        // setCState(response.data.response.state)
        // console.log(dist);
      }).catch();
    };

function search(rows)
{
  return rows.filter((row) =>
    row.name.toLowerCase().indexOf(q) > -1 ||
    row.city.toLowerCase().indexOf(q) > -1 ||
    row.department.toLowerCase().indexOf(q) > -1 ||
    row.email.toLowerCase().indexOf(q) > -1
    );
}

const sData = search(users)
const columns = sData[0] && Object.keys(sData[0])

const Style = { minWidth: "190px" };

const aclasses = useStyles();
  const [aOpen, setAOpen] = useState(false);
  const ahandleClose = () => {
    setAOpen(false);
  };
  const ahandleToggle = () => {
    setAOpen(!aOpen);
  };

  const dclasses = useStyles();
    const [dOpen, setDOpen] = useState(false);
    const dhandleClose = () => {
      setDOpen(false);
    };
    const dhandleToggle = () => {
      setDOpen(!dOpen);
    };

    return (
      <>
        <Header />
        {/* Page content */}
        <Container className='mt--7' fluid>


          {/* Table */}
          <Row>
            <div className='col'>
              <Card className='shadow'>
                <CardHeader className='border-0'>
                  <Input type='text' value={q} onChange={(e) => setQ(e.target.value)}/>
                    <div style={{ display: 'flex', justifyContent: 'center', marginTop: '15px' }}>
                    <div style={{ display: 'flex', justifyContent: 'center', flexDirection: 'column' }}>
                        <Button color="success" onClick={toggle} style={{ marginBottom: '1rem',}}>Add</Button>
                          <Collapse
                          isOpen={isOpen}
                          onExiting={onExiting}
                          >
                          <Card style={{ marginBottom: '20px' }}>
                            <CardBody>
                              <Row form>
                                <Col md={6}>
                                  <FormGroup>
                                    <Label for="examplePassword">Name</Label>
                                    <Input type="text" name="password" placeholder="Name" onChange={set('name')} />
                                  </FormGroup>
                                </Col>
                                <Col md={6}>
                                  <FormGroup>
                                    <Label for="exampleName">Email</Label>
                                    <Input type="email" name="name" placeholder="Email" onChange={set('email')} />
                                  </FormGroup>
                                </Col>
                              </Row>
                              <Row form>
                                <Col md={3}>
                                  <FormGroup>
                                    <Label for="exampleName">Age</Label>
                                    <Input type="text" name="name" placeholder="Age" onChange={set('age')} />
                                  </FormGroup>
                                </Col>
                                <Col md={4}>
                                  <FormGroup>
                                    <Label for="examplePassword">Designation</Label>
                                    <Input type="text" name="password" placeholder="Designation" onChange={set('designation')} />
                                  </FormGroup>
                                </Col>
                                <Col md={5}>
                                  <FormGroup>
                                    <Label for="exampleName">Password</Label>
                                    <Input type="text" name="name" placeholder="Password" onChange={set('password')} />
                                  </FormGroup>
                                </Col>
                              </Row>
                            </CardBody>
                          </Card>
                        </Collapse>
                    </div>
                  </div>
                </CardHeader>
                <Table className='align-items-center table-flush' responsive>
                	<thead className='thead-light'>
                	<tr>
                    <th scope='col'>Id</th>
                    <th scope='col'>Name</th>
                    <th scope='col'>Email</th>
                    <th scope='col'>Age</th>
                    <th scope='col'>Designation</th>
                    <th scope='col'>Password</th>
                    <th scope='col'>Action</th>
                  </tr>

                	</thead>
                  <tbody>
                    {sData.map(row =>
                        <tr>
                            <td>{row.id}</td>
                            <td>{row.name}</td>
                            <td>{row.email}</td>
                            <td>{row.age}</td>
                            <td>{row.designation}</td>
                            <td>{row.password}</td>
                            <td>
                              <Button className='btn-danger' onClick={(e) => deleteItem(row.id)}>Delete</Button>
                              <Button className="btn-warning" onClick={() => editItem(row.id)}>Edit</Button>
                            </td>
                        </tr>
                      )}
                  </tbody>

                </Table>
                <CardFooter className='py-4'>
                  <nav aria-label='...'>
                    <Pagination
                      className='pagination justify-content-end mb-0'
                      listClassName='justify-content-end mb-0'
                    >
                      <PaginationItem className='disabled'>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                          tabIndex='-1'
                        >
                          <i className='fas fa-angle-left' />
                          <span className='sr-only'>Previous</span>
                        </PaginationLink>
                      </PaginationItem>

                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          <i className='fas fa-angle-right' />
                          <span className='sr-only'>Next</span>
                        </PaginationLink>
                      </PaginationItem>
                    </Pagination>
                  </nav>
                </CardFooter>
              </Card>
            </div>
          </Row>
        </Container>
        <Backdrop className={aclasses.backdrop} open={aOpen}>
          <Card className='bcar'>
          <IconContext.Provider value={{ color: "#2dce89", size: '10rem' }}>
            <div className='bcar-body'><FiCheckCircle /></div>
          </IconContext.Provider>
            <CardSubtitle tag="h2" className='bcar-asub'>{alertMessage}</CardSubtitle>
            <CardBody  className='bcar-body'>
              <Button className='btn-success'  onClick={ahandleClose}>Done</Button>
            </CardBody>
          </Card>
        </Backdrop>
        <Backdrop className={dclasses.backdrop} open={dOpen}>
          <Card className='bcar'>
          <IconContext.Provider value={{ color: "#f5365c", size: '10rem' }}>
            <div className='bcar-body'><FiCheckCircle /></div>
          </IconContext.Provider>
            <CardSubtitle tag="h2" className='bcar-dsub'>{alertMessage}</CardSubtitle>
            <CardBody  className='bcar-body'>
              <Button className='btn-danger'  onClick={dhandleClose}>Done</Button>
            </CardBody>
          </Card>
        </Backdrop>
      </>
    );
  }


export default Employee;
