import React, { useState } from 'react';
import { Collapse, Button, CardBody, Card, FormGroup, Label, Input } from 'reactstrap';

const Registered = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  const onExiting = () => console.log('Submit');

  return (
    <div style={{display: 'flex', justifyContent: 'center', flexDirection: 'column'}}>
      <Button color="primary" onClick={toggle} style={{ marginBottom: '1rem', width: '400px' }}>Registration</Button>
        <Collapse
        isOpen={isOpen}
        onExiting={onExiting}
        >
        <Card style={{width: '400px', marginBottom: '20px'}}>
          <CardBody>
            <FormGroup>
              <Label for="exampleSelect">Select State</Label>
              <Input type="select" name="select" id="exampleSelect">
                <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option>
                <option>5</option>
              </Input>
            </FormGroup>
          </CardBody>
        </Card>
      </Collapse>
    </div>
  );
}

export default Registered;
