import React, { useEffect, useState } from 'react';
import axios from 'axios';
import history from '../../../history.js';
import { Col, Row, Button, Form, FormGroup, Label, Input, Card, CardBody, CardSubtitle } from 'reactstrap';

import Header from '../../Headers/Header.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const CtUpdate = (props) => {
  const [values, setValues] = useState('');
  const [cities, setCities] = useState([])
  const [alertMessage, setAlertMessage] = useState("")
  const [open, setOpen] = useState(false);
  const classes = useStyles();
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };

    let CityData = sessionStorage.getItem('cityData')
    CityData = JSON.parse(CityData)


    window.onload = function() {
    	if(!window.location.hash) {
    		window.location = window.location + '#loaded';
    		window.location.reload();
    	}
    }

  useEffect(() => {
          if(!getData())
          {
            getData();
          }
  }, [])

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` },
  };

  const bodyParameters = {
     key: "value"
  };

  const getData = () => {
      setValues({
        state_id: CityData.state_id, dist_id: CityData.dist_id, name: CityData.name,
      });
  }

  const Certificate = cities;

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const UpdateItem = () => {

    axios.put(
      'https://businessbook.co.in/api/api/city/',
      values,
      config,
    ).then((response)=> {
      // setCities(response.data.response.cities)
      console.log(response);
      setAlertMessage(response.data.response.message);
      handleToggle();

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
    props.history.push('/admin/city')
  }

  return (
    <div>
      <Header />

    <Form style={{margin: '70px'}}>
      <FormGroup>
        <Label for="exampleID">Sate ID</Label>
        <Input type="text" name="id" id="exampleID" value={CityData.state_id} disabled />
      </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleName">Dist ID</Label>
              <Input type="text" name="name" id="exampleName" defaultValue={CityData.dist_id} onChange={set('dist_id')} />
            </FormGroup>
          </Col>
        </Row>
        <FormGroup>
          <Label for="exampleNumber">City Name</Label>
          <Input type="text" name="number" id="exampleNumber" defaultValue={CityData.name} onChange={set('name')} />
        </FormGroup>

      <Button className='btn-warning bcentr' onClick={() => UpdateItem()}>Update</Button>
    </Form>

    <Backdrop className={classes.backdrop} open={open}>
      <Card className='bcar'>
      <IconContext.Provider value={{ color: "#fb6340", size: '10rem' }}>
        <div className='bcar-body'><FiCheckCircle /></div>
      </IconContext.Provider>
        <CardSubtitle tag="h2" className='bcar-usub'>{alertMessage}</CardSubtitle>
        <CardBody  className='bcar-body'>
          <Button className='btn-warning'  onClick={handleClose} href='/admin/City'>Done</Button>
        </CardBody>
      </Card>
    </Backdrop>
  </div>
  );
}

export default CtUpdate;
