import React, { useEffect, useState, useCallback } from 'react';
import axios from 'axios';
import history from '../../history.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

import { CopyToClipboard } from 'react-copy-to-clipboard';
// reactstrap components
import {
  Badge,
  Button,
  Card,
  CardHeader,
  CardFooter,
  CardBody,
  CardSubtitle,
  DropdownMenu,
  DropdownItem,
  Input,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  Container,
  Row,
  UncontrolledTooltip
} from 'reactstrap';

import Header from '../../components/Headers/Header.js';

require('es6-promise').polyfill();
require('isomorphic-fetch')
// core components

const token = 'YWRtaW5hZG1pbjEyMw==';
const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));
// const token = JSON.parse(sessionStorage.getItem('YWRtaW5hZG1pbjEyMw=='));

let page1=0;
function Customers(props){
  const [isOpen, setIsOpen] = useState(false);
  const [alertMessage, setAlertMessage] = useState("");
  const [users, setUsers] = useState([])
  const [q, setQ]  = useState('')
  const [page, setPage] = useState('00')

  useEffect(() => {
      getData();

  }, [])

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const pageNext = () => {
    //setPage('10+10');
    if(page1>=0)
    {
      page1=page1+10;
      getData()
    }
    else {
      alert("top end")
    }
  }

  const pagePrev = () => {
    //setPage('00');
    if(page1>=0)
    {
      page1=page1-10;
      getData()
    }else {
      alert("end")
    }
  }

// sessionStorage.setItem('Pg1', JSON.stringify(page1));
//
// let Page1 = sessionStorage.getItem('Pg1')
// Page1 = JSON.parse(Page1)
let Page1 = page1

  const data = {
    page: Page1
  };

const getData = () =>{
  axios.post(
    'https://businessbook.co.in/api/api/clientUser/read.php',
    data,
    config
  ).then((response)=> {
    // setUsers(response.data.response.clientUsers)
    console.log(response.data.response.clientUsers)
    if(response.data.response.message == true){
      setUsers(response.data.response.clientUsers)
    }
    else{
      alert(response.data.response.message)
    }
  }).catch();
}

//   const getData = async () => {
//
//       const response = await axios.get(URL, { headers: { Authorization: "Bearer " + token,
//   Accept: "application/json, text/plain, */*",
//   "Content-Type": "application/json"
// } })
//       setUsers(response.data)
//       console.log(response.data)
//   }

const deleteItem = (id, e) => {

  // axios.post("https://businessbook.co.in/api/api/user/", id)
  axios.delete(`https://businessbook.co.in/api/api/clientUser/`, {
  headers: {
    Authorization: `Bearer ${token}`
    },
    data: {
      id: id
    }
  }).then((response)=> {
        //   const sData = this.state.sData.filter(item => item.id !== id);
        // this.setState({ sData });
      console.log(response);
      setAlertMessage(response.data.response.message);
      dhandleToggle();
      getData();
      }).catch();
    }



  const editItem = (id) => {
    sessionStorage.setItem('custID', JSON.stringify(id));
    props.history.push('/admin/CustomerEdit')
  }

function search(rows)
{
  return rows.filter((row) =>
    row.name.toLowerCase().indexOf(q) > -1 ||
    row.city.toLowerCase().indexOf(q) > -1 ||
    row.department.toLowerCase().indexOf(q) > -1 ||
    row.email.toLowerCase().indexOf(q) > -1
    );
}

const sData = search(users)
const columns = sData[0] && Object.keys(sData[0])

const Style = { minWidth: "190px" };

  const dclasses = useStyles();
    const [dOpen, setDOpen] = useState(false);
    const dhandleClose = () => {
      setDOpen(false);
    };
    const dhandleToggle = () => {
      setDOpen(!dOpen);
    };

    return (
      <>
        <Header />
        {/* Page content */}
        <Container className='mt--7' fluid>

          {/* Table */}
          <Row>
            <div className='col'>
              <Card className='shadow'>
                <CardHeader className='border-0'>
                  <Input type='text' value={q} onChange={(e) => setQ(e.target.value)}/>
                </CardHeader>
                <Table className='align-items-center table-flush' responsive>
                	<thead className='thead-light'>
                	<tr>
                    <th scope='col'>Id</th>
                    <th scope='col'>Name</th>
                    <th scope='col'>Mobile Number</th>
                    <th scope='col'>Longitude</th>
                    <th scope='col'>Latitude</th>
                    <th scope='col'>Timestamp</th>
                    <th scope='col'>Action</th>
                  </tr>

                	</thead>
                  <tbody>
                    {sData.map(row =>
                        <tr>
                            <td>{row.id}</td>
                            <td>{row.name}</td>
                            <td>{row.mobile}</td>
                            <td>{row.longitude}</td>
                            <td>{row.latitude}</td>
                            <td>{row.timestamp}</td>
                            <td>
                              <Button className='btn-danger' onClick={(e) => deleteItem(row.id)}>Delete</Button>
                              <Button className="btn-warning" onClick={() => editItem(row.id)}>Edit</Button>
                            </td>
                        </tr>
                  )}
                  </tbody>

                </Table>
                <CardFooter className='py-4'>
                  <nav aria-label='...'>
                    <Pagination
                      className='pagination justify-content-end mb-0'
                      listClassName='justify-content-end mb-0'
                    >
                      <PaginationItem>
                        <PaginationLink
                          onClick={pagePrev}
                          tabIndex='-1'
                        >
                          <i className='fas fa-angle-left' />
                          <span className='sr-only'>Previous</span>
                        </PaginationLink>
                      </PaginationItem>

                      <PaginationItem>
                        <PaginationLink
                          onClick={pageNext}
                        >
                          <i className='fas fa-angle-right' />
                          <span className='sr-only'>Next</span>
                        </PaginationLink>
                      </PaginationItem>
                    </Pagination>
                  </nav>
                </CardFooter>
              </Card>
            </div>
          </Row>
        </Container>
        <Backdrop className={dclasses.backdrop} open={dOpen}>
          <Card className='bcar'>
          <IconContext.Provider value={{ color: "#f5365c", size: '10rem' }}>
            <div className='bcar-body'><FiCheckCircle /></div>
          </IconContext.Provider>
            <CardSubtitle tag="h2" className='bcar-dsub'>{alertMessage}</CardSubtitle>
            <CardBody  className='bcar-body'>
              <Button className='btn-danger'  onClick={dhandleClose}>Done</Button>
            </CardBody>
          </Card>
        </Backdrop>
      </>
    );
  }


export default Customers;



