import React, { useEffect, useState } from 'react';
import axios from 'axios';
import history from '../../../history.js';
import { Col, Row, Button, Form, FormGroup, Label, Input, Card, CardSubtitle, CardImg, CardBody, CardTitle } from 'reactstrap';

import Header from '../../Headers/Header.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const SUpdate = (props) => {
  const [values, setValues] = useState('');
  const [states, setStates] = useState([]);
  const [alertMessage, setAlertMessage] = useState("")
  const [open, setOpen] = useState(false);
  const classes = useStyles();
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };


    let StateID = sessionStorage.getItem('stateID')
    StateID = JSON.parse(StateID)

    let NState = sessionStorage.getItem('state')
    NState = JSON.parse(NState)

    window.onload = function() {
    	if(!window.location.hash) {
    		window.location = window.location + '#loaded';
    		window.location.reload();
    	}
    }

  useEffect(() => {
          if(!getData())
          {
            getData();
          }
  }, [])

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` },
  };

  const bodyParameters = {
     key: "value"
  };

  const data = {
     state_id: StateID
  };

  const getData = () => {
    axios.post(
      'https://businessbook.co.in/api/api/state/getState.php',
      data,
      config,
    ).then((response)=> {
      setStates(response.data.response.state);
      sessionStorage.setItem('state', JSON.stringify(response.data.response.state));
      console.log(NState);
      setValues({
        state_id: StateID, dist_id: NState.dist_id, name: NState.name, timestamp: NState.timestamp,
      });
    }).catch();
  }

  const State = states;

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const UpdateItem = () => {

    axios.put(
      'https://businessbook.co.in/api/api/state/',
      values,
      config,
    ).then((response)=> {
      // setStates(response.data.response.states)
      console.log(response);
      setAlertMessage(response.data.response.message);
      handleToggle();

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
  }


  return (
    <div>
      <Header />

    <Form style={{margin: '70px'}}>
      <FormGroup>
        <Label for="exampleID">State ID</Label>
        <Input type="text" name="id" id="exampleID" value={State.state_id} disabled />
      </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleName">Dist ID</Label>
              <Input type="text" name="name" id="exampleName" defaultValue={State.dist_id} onChange={set('dist_id')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="examplePassword">Name</Label>
              <Input type="text" name="password" id="examplePassword" defaultValue={State.name} onChange={set('name')} />
            </FormGroup>
          </Col>
        </Row>
        <FormGroup>
          <Label for="exampleNumber">Timestamp</Label>
          <Input type="tel" name="number" id="exampleNumber" defaultValue={State.timestamp} onChange={set('timestamp')} />
        </FormGroup>

      <Button className='btn-warning bcentr' onClick={() => UpdateItem()}>Update</Button>
    </Form>

    <Backdrop className={classes.backdrop} open={open}>
      <Card className='bcar'>
      <IconContext.Provider value={{ color: "#fb6340", size: '10rem' }}>
        <div className='bcar-body'><FiCheckCircle /></div>
      </IconContext.Provider>
        <CardSubtitle tag="h2" className='bcar-usub'>{alertMessage}</CardSubtitle>
        <CardBody  className='bcar-body'>
          <Button className='btn-warning'  onClick={handleClose} href='/admin/State'>Done</Button>
        </CardBody>
      </Card>
    </Backdrop>
  </div>
  );
}

export default SUpdate;
