import React, {useEffect, useState} from 'react';
import axios from 'axios';

import {
  Badge,
  Button,
  Card,
  CardHeader,
  CardFooter,
  DropdownMenu,
  DropdownItem,
  Input,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  Container,
  Row,
  UncontrolledTooltip
} from 'reactstrap';

import Header from '../../components/Headers/Header.js';

require('es6-promise').polyfill();
require('isomorphic-fetch')
// core components

const URL = 'https://businessbook.co.in/api/api/user/read.php'
const token = 'YWRtaW5hZG1pbjEyMw==';


const API_HOST = "http://localhost:3000";
const INVENTORY_API_URL = `${API_HOST}/inventory`;

const config = {
    headers: { Authorization: `Bearer ${token}` }
};

const bodyParameters = {
   key: "value"
};

function Edit() {
    const [data, setData] = useState([]);

    const fetchInventory = () => {
      axios.post(
        'https://businessbook.co.in/api/api/user/read.php',
        bodyParameters,
        config
      ).then((response)=> {
        setData(response.data.response.users)
        console.log(response.data.response.users)
      }).catch();
    }

    useEffect(() => {
        fetchInventory();
    }, []);


    const [inEditMode, setInEditMode] = useState({
        status: true,
        rowKey: null
    });

    const [department, setDepartment] = useState(null);

    /**
     *
     * @param id - The id of the product
     * @param currentDepartment - The current unit price of the product
     */
    const onEdit = ({id, currentDepartment}) => {
        setInEditMode({
            status: true,
            rowKey: id,
        })
        setDepartment(currentDepartment);
    }

    /**
     *
     * @param id
     * @param newDepartment
     */
    const updateInventory = ({id, newDepartment, name}) => {
        axios.put(`${URL}/${id}`, {
            body: JSON.stringify({
                id: id,
                name: name,
                department: newDepartment
            }),
            headers: {
                "Content-type": "application/json; charset=UTF-8"
            }
        })
            .then(response => response.json())
            .then(json => {
                // reset inEditMode and unit price state values
                onCancel();

                // fetch the updated data
                fetchInventory();
            })
    }

    /**
     *
     * @param id -The id of the product
     * @param newDepartment - The new unit price of the product
     */
    const onSave = ({id, newDepartment}) => {
        updateInventory({id, newDepartment});
    }

    const onCancel = () => {
        // reset the inEditMode state value
        setInEditMode({
            status: false,
            rowKey: null
        })
        // reset the unit price state value
        setDepartment(null);
    }

    return (
      <>
      <Header />

        <div className="container">
            <h1>Simple Inventory Table</h1>
            <Table>
                <thead>
                <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>City</th>
                    <th>Department</th>
                    <th>Action</th>
                </tr>
                </thead>
                <tbody>
                {
                    data.map((item) => (
                        <tr key={item.id}>
                            <td>{item.id}</td>
                            <td>{item.name}</td>
                            <td>{item.city}</td>
                            <td>
                                {
                                    inEditMode.status && inEditMode.rowKey === item.id ? (
                                        <input type= 'text' value={item.department}
                                               onChange={(event) => setDepartment(event.target.value)}
                                        />
                                    ) : (
                                        item.department
                                    )
                                }
                            </td>
                            <td>
                                {
                                    inEditMode.status && inEditMode.rowKey === item.id ? (
                                        <React.Fragment>
                                            <Button
                                                className={"btn-success"}
                                                onClick={() => onSave({id: item.id, newDepartment: department})}
                                            >
                                                Save
                                            </Button>

                                            <Button
                                                className={"btn-secondary"}
                                                style={{marginLeft: 8}}
                                                onClick={() => onCancel()}
                                            >
                                                Cancel
                                            </Button>
                                        </React.Fragment>
                                    ) : (
                                        <Button
                                            className={"btn-primary"}
                                            onClick={() => onEdit({id: item.id, currentDepartment: item.department})}
                                        >
                                            Edit
                                        </Button>
                                    )
                                }
                            </td>
                        </tr>
                    ))
                }
                </tbody>
            </Table>
        </div>
        </>
    );
}

export default Edit;
