import React, { useState, useEffect } from 'react';
import Paper from '@material-ui/core/Paper';
import { EditingState } from '@devexpress/dx-react-grid';
import {
  Grid,
  Table,
  TableHeaderRow,
  TableEditRow,
  TableEditColumn,
} from '@devexpress/dx-react-grid-material-ui';

import axios from 'axios'

import Header from '../../components/Headers/Header.js';

require('es6-promise').polyfill();
require('isomorphic-fetch')
// core components

const URL = 'https://businessbook.co.in/api/api/user/read.php'
const token = 'YWRtaW5hZG1pbjEyMw==';

//
// import {
//   generateRows,
//   defaultColumnValues,
// } from '../../../demo-data/generator';

const getRowId = row => row.id;

export default () => {
  const [columns] = useState([
    { name: 'id', title: 'ID' },
    { name: 'name', title: 'Name' },
    { name: 'city', title: 'City' },
    { name: 'department', title: 'Department' },
    { name: 'working_hour', title: 'Working Hours' },
  ]);
  const [rows, setRows] = useState([]);

  useEffect(() => {
      getData()
  }, [])

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const bodyParameters = {
     key: "value"
  };

const getData = () =>{
  axios.post(
    'https://businessbook.co.in/api/api/user/read.php',
    bodyParameters,
    config
  ).then((response)=> {
    setRows(response.data.response.users)
    // console.log(response.data.response.users)
  }).catch();
}

function filter_array(test_array) {
    let index = -1;
    const arr_length = test_array ? test_array.length : 0;
    let resIndex = -1;
    const result = [];

    while (++index < arr_length) {
        const value = test_array[index];

        if (value) {
            result[++resIndex] = value;
        }
    }

    return result;
}

  const commitChanges = ({ added, changed, deleted }) => {
    let changedRows;
    if (added) {
      const startingAddedId = rows.length > 0 ? rows[rows.length - 1].id + 1 : 0;
      changedRows = [
        ...rows,
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
      ];
    }
    if (changed) {

      const chngd = rows.map(row => ( changed[row.id] ))
      console.log(filter_array(chngd));


      chngd.map(function(item, i){
        // console.log(item);
        // if(item.)
        // {
        //
        // }
        //console.log(i);
        // return <li key={i}>Test</li>
      })

      // console.log(chngd)
      changedRows = rows.map(row => (changed[row.id] ? { ...row, ...changed[row.id] } : row));
      // console.log((row => (changed[row.id])));
    }
    if (deleted) {
// console.log(getRowId);
      const deletedSet = new Set(deleted);
      changedRows = rows.filter(row => !deletedSet.has(row.id));
      // console.log(row.id);
      //
      // axios.delete(`https://businessbook.co.in/api/api/user/`, {
      // headers: {
      //   Authorization: `Bearer ${token}`
      //   },
      //   data: {
      //     id: id
      //   }
      // }).then((response)=> {
      //       //   const sData = this.state.sData.filter(item => item.id !== id);
      //       // this.setState({ sData });
      //     console.log(response)
      //     }).catch();
    }
    setRows(changedRows);
  };


  return (
    <>
    <Header />
    <Paper>
      <Grid
        rows={rows}
        columns={columns}
        getRowId={getRowId}
      >
        <EditingState
          onCommitChanges={commitChanges}
        />
        <Table />
        <TableHeaderRow />
        <TableEditRow />
        <TableEditColumn
          showAddCommand
          showEditCommand
          showDeleteCommand
        />
      </Grid>
    </Paper>
    </>

  );
};
