import React, { useState, useEffect } from 'react';
import axios from 'axios';
import history from '../../../history.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardHeader,
  CardFooter,
  DropdownMenu,
  DropdownItem,
  Input,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Pagination,
  PaginationItem,
  PaginationLink,
  Progress,
  Table,
  Container,
  Row,
  UncontrolledTooltip
} from 'reactstrap';

import Dadd from './Dadd.js'

import Header from '../../Headers/Header.js';
require('es6-promise').polyfill();
require('isomorphic-fetch')

const token = 'YWRtaW5hZG1pbjEyMw==';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const Department = (props) => {

  const [isOpen, setIsOpen] = useState(false);
  const [departments, setDepartments] = useState([])
  const [q, setQ]  = useState('')
  const [alertMessage, setAlertMessage] = useState("")

  useEffect(() => {
      getData()
  }, [])

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const bodyParameters = {
     key: "value"
  };

const getData = () =>{
  axios.post(
    'https://businessbook.co.in/api/api/department/read.php',
    bodyParameters,
    config
  ).then((response)=> {
    setDepartments(response.data.response.department)
    console.log(response.data.response.department)
  }).catch();
}

const deleteItem = (id, e) => {

  // axios.post("https://businessbook.co.in/api/api/user/", id)
  axios.delete(`https://businessbook.co.in/api/api/department/`, {
  headers: {
    Authorization: `Bearer ${token}`
    },
    data: {
      id: id
    }
  }).then((response)=> {
        //   const sData = this.state.sData.filter(item => item.id !== id);
        // this.setState({ sData });
        console.log(response.data.response.message);
        setAlertMessage(response.data.response.message);
        dhandleToggle();
        getData();
      }).catch();
    }



  const editItem = (id) => {
    sessionStorage.setItem('deptID', JSON.stringify(id));
    props.history.push('/admin/DUpdate')
    window.location.reload(false)

  }


function search(rows)
{
  return rows.filter((row) =>
    row.id.toLowerCase().indexOf(q) > -1 ||
    row.department.toLowerCase().indexOf(q) > -1 ||
    row.timestamp.toLowerCase().indexOf(q) > -1
    );
}

const sData = search(departments)
const columns = sData[0] && Object.keys(sData[0])

  const toggle = () => setIsOpen(!isOpen);

  const Style = { minWidth: "190px" };
  const classes = useStyles();
    const [open, setOpen] = useState(false);
    const handleClose = () => {
      setOpen(false);
    };
    const dhandleToggle = () => {
      setOpen(!open);
    };

  return (
    <>
      <Header />

        <div>
          <Row>
            <div className='col'>
              <Card className='shadow'>
                <CardHeader className='border-0'>
                  <Input type='text' value={q} onChange={(e) => setQ(e.target.value)}/>
                  <div style={{display: 'flex', justifyContent: 'center', marginTop: '15px'}}><Dadd /></div>
                </CardHeader>
                <Table className='align-items-center table-flush' responsive >
                  <thead className='thead-light'>
                  <tr>
                    <th style={Style} scope='col'>ID</th>
                    <th style={Style} scope='col'>Department</th>
                    <th style={Style} scope='col'>Timestamp</th>
                    <th style={Style} scope='col'>Action</th>
                  </tr>

                  </thead>
                  <tbody>
                    {sData.map(row =>
                        <tr>
                            <td>{row.id}</td>
                            <td>{row.department}</td>
                            <td>{row.timestamp}</td>
                            <td>
                              <Button className='btn-danger' onClick={(e) => deleteItem(row.id)}>Delete</Button>
                              <Button className="btn-warning" onClick={() => editItem(row.id)}>Edit</Button>
                            </td>
                        </tr>
                  )}
                  </tbody>

                </Table>
                <CardFooter className='py-4'>
                  <nav aria-label='...'>
                    <Pagination
                      className='pagination justify-content-end mb-0'
                      listClassName='justify-content-end mb-0'
                    >
                      <PaginationItem className='disabled'>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                          tabIndex='-1'
                        >
                          <i className='fas fa-angle-left' />
                          <span className='sr-only'>Previous</span>
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem className='active'>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          1
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          2 <span className='sr-only'>(current)</span>
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          3
                        </PaginationLink>
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          href='#pablo'
                          onClick={e => e.preventDefault()}
                        >
                          <i className='fas fa-angle-right' />
                          <span className='sr-only'>Next</span>
                        </PaginationLink>
                      </PaginationItem>
                    </Pagination>
                  </nav>
                </CardFooter>
              </Card>
            </div>
          </Row>

          <Backdrop className={classes.backdrop} open={open}>
            <Card className='bcar'>
            <IconContext.Provider value={{ color: "#f5365c", size: '10rem' }}>
              <div className='bcar-body'><FiCheckCircle /></div>
            </IconContext.Provider>
              <CardSubtitle tag="h2" className='bcar-dsub'>{alertMessage}</CardSubtitle>
              <CardBody  className='bcar-body'>
                <Button className='btn-danger'  onClick={handleClose}>Done</Button>
              </CardBody>
            </Card>
          </Backdrop>
      </div>

    </>
  );
}

export default Department;
