import React, { useState } from 'react';

import axios from 'axios';
import { Collapse, Button, CardBody, CardSubtitle, Card, FormGroup, Label, Input } from 'reactstrap';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const CTadd = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const [cstate, setCState] = useState([]);
  const [alertMessage, setAlertMessage] = useState("")

  const [values, setValues] = useState({
    state_id: '', name: '',
  });

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const token = 'YWRtaW5hZG1pbjEyMw==';
  const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const bodyParameters = {
     key: "value",
  };

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  const onEntering = () => {
        axios.post(
          'https://businessbook.co.in/api/api/state/getAllState.php',
          bodyParameters,
          config,

          // data
        ).then((response)=> {
          // setUsers(response.data.response.users)
          console.log(response.data.response.state);
          setCState(response.data.response.state);

          // console.log(dist);
        }).catch();
      };

      const onExiting = () => {
        axios.post(
          'https://businessbook.co.in/api/api/city/',
          values,
          config,
        ).then((response)=> {
          // setUsers(response.data.response.users)
          console.log(response);
          setAlertMessage(response.data.response.message);
          handleToggle();

          // setCState(response.data.response.state)
          // console.log(dist);
        }).catch();
        console.log(values);
      }

      const classes = useStyles();
        const [open, setOpen] = useState(false);
        const handleClose = () => {
          setOpen(false);
        };
        const handleToggle = () => {
          setOpen(!open);
        };

  const cState = cstate;

  return (
    <div style={{display: 'flex', justifyContent: 'center', flexDirection: 'column'}}>
      <Button color="success" onClick={toggle} style={{ marginBottom: '1rem', width: '400px' }}>Add</Button>
        <Collapse
        isOpen={isOpen}
        onEntering={onEntering}
        onExiting={onExiting}
        >
        <Card style={{width: '400px', marginBottom: '20px'}}>
          <CardBody>
          <FormGroup>
            <Label for="exampleSelect">Select State</Label>
            <Input type="select" name="select" id="exampleSelect" value={values.state_id} onChange={set('state_id')} >
              {cState.map(row =>
                <option value={row.state_id} data-id={row.state_id} key={row.state_id} >{row.name}</option>
                  )}

            </Input>
          </FormGroup>
          <FormGroup>
            <Label for="exampleSelect">Enter City</Label>
            <Input type="text" name="City" value={values.name} onChange={set('name')} />
          </FormGroup>

          </CardBody>
        </Card>
      </Collapse>

      <Backdrop className={classes.backdrop} open={open}>
        <Card className='bcar'>
        <IconContext.Provider value={{ color: "#2dce89", size: '10rem' }}>
          <div className='bcar-body'><FiCheckCircle /></div>
        </IconContext.Provider>
          <CardSubtitle tag="h2" className='bcar-sub'>{alertMessage}</CardSubtitle>
          <CardBody  className='bcar-body'>
            <Button className='btn-danger'  onClick={handleClose} href='/admin/City'>Done</Button>
          </CardBody>
        </Card>
      </Backdrop>
    </div>
  );
};

export default CTadd;
