import React, { useState } from 'react';
import axios from 'axios';
import { Collapse, Button, CardBody, Card, FormGroup, Label, Input } from 'reactstrap';

const Stateo = (props) => {
  const [isOpen, setIsOpen] = useState(false);
  const [cstate, setCState] = useState([]);

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` }
  };

  const bodyParameters = {
     key: "value"
  };

  const data = {
    "dist_id":0

      };

  const toggle = () => {
    axios.post(
      'https://businessbook.co.in/api/api/state/getAllState.php',
      bodyParameters,
      config,
      // data
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response.data.response.state);
      setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    setIsOpen(!isOpen);
  }

  const cState = cstate;

  const onExiting = () => console.log('Submit');

  return (
    <div style={{display: 'flex', justifyContent: 'center', flexDirection: 'column'}}>
      <Button color="primary" onClick={toggle} style={{ marginBottom: '1rem', width: '400px' }}>State</Button>
        <Collapse
        isOpen={isOpen}
        onExiting={onExiting}
        >
        <Card style={{width: '400px', marginBottom: '20px'}}>
          <CardBody>
          <FormGroup>
            <Label for="exampleSelect">Select State</Label>
            <Input type="select" name="select" id="exampleSelect">
            {cState.map(row =>
              <option>{row.name}</option>
                  )}

            </Input>
          </FormGroup>

          </CardBody>
        </Card>
      </Collapse>
    </div>
  );
}

export default Stateo;
