import React from 'react';
import ReactDOM from 'react-dom';
import { HashRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
// import { Router } from 'react-router';

import './assets/plugins/nucleo/css/nucleo.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import './assets/scss/argon-dashboard-react.scss';

import AdminLayout from './layouts/Admin.js';
import AuthLayout from './layouts/Auth.js';

import Index from './views/Index.js';
import history from './history.js';

import EmployeeEdit from './components/TableEdit/EmployeeEdit';
import ProviderEdit from './components/TableEdit/ProviderEdit';
import CustomerEdit from './components/TableEdit/CustomerEdit';

import SUpdate from './components/Options/State/SUpdate';
import CtUpdate from './components/Options/City/CtUpdate';
import DUpdate from './components/Options/Department/DUpdate';
import CUpdate from './components/Options/Certificate/CUpdate';

ReactDOM.render(
  <Router history={history}>
    <Switch>
      <Route path='/admin' render={props => <AdminLayout {...props} />} />
      <Route path='/auth' render={props => <AuthLayout {...props} />} />

      <Redirect from='/' to='/auth/login' />

      <Route path="/admin/index" exact component={Index} />
      <Route path="/admin/EmployeeEdit" exact component={EmployeeEdit} />
      <Route path="/ProviderEdit" exact component={ProviderEdit} />
      <Route path="/CustomerEdit" exact component={CustomerEdit} />

      <Route path="/SUpdate" exact component={SUpdate} />
      <Route path="/CtUpdate" exact component={CtUpdate} />
      <Route path="/DUpdate" exact component={DUpdate} />
      <Route path="/CUpdate" exact component={CUpdate} />

    </Switch>
  </Router>,
  document.getElementById('root')
);
