import React, { useEffect, useState } from 'react';
import axios from 'axios'
import history from '../../history.js';
import { Col, Row, Button, Form, FormGroup, Label, Input, Card, CardBody, CardSubtitle } from 'reactstrap';

import Header from '../Headers/Header.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const CustomerEdit = (props) => {
  const [values, setValues] = useState('');
  const [alertMessage, setAlertMessage] = useState("")
  const [open, setOpen] = useState(false);
  const classes = useStyles();
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };

    let CustID = sessionStorage.getItem('custID')
    CustID = JSON.parse(CustID)

    let NCust = sessionStorage.getItem('customer')
    NCust = JSON.parse(NCust)

  const [users, setUsers] = useState([])

  window.onload = function() {
    if(!window.location.hash) {
      window.location = window.location + '#loaded';
      window.location.reload();
    }
  }

useEffect(() => {
        if(!getData())
        {
          getData();
        }
}, [])

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` },

  };

  const bodyParameters = {
     key: "value"
  };

  const data = {
     id: CustID
  };

  const getData = () =>{
    axios.post(
      'https://businessbook.co.in/api/api/clientUser/single_read.php',
      data,
      config,
    ).then((response)=> {
      setUsers(response.data.response.clientUsers)
      sessionStorage.setItem('customer', JSON.stringify(response.data.response.clientUsers));
      console.log(NCust);
      setValues({
        id: CustID, name: NCust.name, mobile: NCust.mobile, longitude: NCust.longitude, latitude: NCust.latitude, password: NCust.password
      });
    }).catch();
  }



  const User = users;

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const UpdateItem = () => {

    axios.put(
      'https://businessbook.co.in/api/api/clientUser/',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response);
      setAlertMessage(response.data.response.message);
      handleToggle();

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
  }

  return (
    <div>
      <Header />

    <Form style={{margin: '70px'}}>
      <FormGroup>
        <Label for="exampleID">ID</Label>
        <Input type="text" name="id" id="exampleID" value={User.id} disabled />
      </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleName">Name</Label>
              <Input type="text" name="name" id="exampleName" defaultValue={User.name} onChange={set('name')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="examplePassword">Password</Label>
              <Input type="password" name="password" id="examplePassword" defaultValue={User.password} onChange={set('password')} />
            </FormGroup>
          </Col>
        </Row>
        <FormGroup>
          <Label for="exampleNumber">Mobile Number</Label>
          <Input type="tel" name="number" id="exampleNumber" defaultValue={User.mobile} onChange={set('number')} />
        </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleLatitude">Latitude</Label>
              <Input type="text" name="latitude" id="exampleLatitude" defaultValue={User.latitude} onChange={set('latitude')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleLongitude">Longitude</Label>
              <Input type="text" name="longitude" id="exampleLongitude" defaultValue={User.longitude} onChange={set('longitude')} />
            </FormGroup>
          </Col>
        </Row>
      <Button className='btn-warning bcentr' onClick={() => UpdateItem()}>Update</Button>
    </Form>

    <Backdrop className={classes.backdrop} open={open}>
      <Card className='bcar'>
      <IconContext.Provider value={{ color: "#fb6340", size: '10rem' }}>
        <div className='bcar-body'><FiCheckCircle /></div>
      </IconContext.Provider>
        <CardSubtitle tag="h2" className='bcar-usub'>{alertMessage}</CardSubtitle>
        <CardBody  className='bcar-body'>
          <Button className='btn-warning'  onClick={handleClose} href='/admin/Customers'>Done</Button>
        </CardBody>
      </Card>
    </Backdrop>
  </div>
  );
}

export default CustomerEdit;
