import React, { useEffect, useState } from 'react';
import axios from 'axios';
import history from '../../history.js';
import { Col, Row, Button, Form, FormGroup, Label, Input, Card, CardBody, CardSubtitle } from 'reactstrap';

import Header from '../Headers/Header.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const EmployeeEdit = (props) => {
  const [values, setValues] = useState('');
  const [alertMessage, setAlertMessage] = useState("")
  const [open, setOpen] = useState(false);
  const classes = useStyles();
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };

    let EmpID = sessionStorage.getItem('empID')
    EmpID = JSON.parse(EmpID)

    let NEmp = sessionStorage.getItem('employee')
    NEmp = JSON.parse(NEmp)

  const [users, setUsers] = useState([])

  window.onload = function() {
    if(!window.location.hash) {
      window.location = window.location + '#loaded';
      window.location.reload();
    }
  }

useEffect(() => {
        if(!getData())
        {
          getData();
        }
}, [])

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` },

  };

  const bodyParameters = {
     key: "value"
  };

  const data = {
     id: EmpID
  };

  const getData = () =>{
    axios.post(
      'https://businessbook.co.in/api/api/employees/single_read.php',
      data,
      config,
    ).then((response)=> {
      setUsers(response.data.response.employee)
      sessionStorage.setItem('employee', JSON.stringify(response.data.response.employee));
      console.log(NEmp);
      setValues({
        id: EmpID, name: NEmp.name, email: NEmp.email, age: NEmp.age, designation: NEmp.designation, password: NEmp.password
      });
    }).catch();
  }

  const User = users;

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const UpdateItem = () => {

    axios.put(
      'https://businessbook.co.in/api/api/employees/',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response);
      setAlertMessage(response.data.response.message);
      handleToggle();

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
  }

  return (
    <div>
      <Header />

    <Form style={{margin: '70px'}}>
      <FormGroup>
        <Label for="exampleID">ID</Label>
        <Input type="text" name="id" id="exampleID" value={User.id} disabled />
      </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleName">Name</Label>
              <Input type="text" name="name" id="exampleName" defaultValue={User.name} onChange={set('name')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="examplePassword">Password</Label>
              <Input type="password" name="password" id="examplePassword" defaultValue={User.password} onChange={set('password')} />
            </FormGroup>
          </Col>
        </Row>
        <FormGroup>
          <Label for="exampleEmail">Eamil</Label>
          <Input type="email" name="email" id="exampleEmail" defaultValue={User.email} onChange={set('email')} />
        </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleAge">Age</Label>
              <Input type="number" name="age" id="exampleAge" defaultValue={User.age} onChange={set('age')} />
            </FormGroup>
          </Col>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleDesign">designation</Label>
              <Input type="text" name="designation" id="exampleDesign" defaultValue={User.designation} onChange={set('designation')} />
            </FormGroup>
          </Col>
        </Row>
      <Button className='btn-warning bcentr' onClick={() => UpdateItem()}>Update</Button>
    </Form>

    <Backdrop className={classes.backdrop} open={open}>
      <Card className='bcar'>
      <IconContext.Provider value={{ color: "#fb6340", size: '10rem' }}>
        <div className='bcar-body'><FiCheckCircle /></div>
      </IconContext.Provider>
        <CardSubtitle tag="h2" className='bcar-usub'>{alertMessage}</CardSubtitle>
        <CardBody  className='bcar-body'>
          <Button className='btn-warning'  onClick={handleClose} href='/admin/Employee'>Done</Button>
        </CardBody>
      </Card>
    </Backdrop>
  </div>
  );
}

export default EmployeeEdit;
