import React, { useState, useEffect } from 'react';
import axios from 'axios';
import history from '../../history.js';

import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col
} from 'reactstrap';

const Login = (props) => {

  const [values, setValues] = useState({
    email: '', password: '',
  });

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}`,
        'content-type': 'application/x-www-form-urlencoded',
      },
    };

    useEffect(() => {
      let Log = sessionStorage.getItem('log')
      Log = JSON.parse(Log)
        if(Log != true){
          props.history.push("/auth/login");
        }

    }, [])

  const Abc = () => {
    axios.post(
      'https://businessbook.co.in/api/api/employees/login.php',
      values,
      config,
    ).then((response)=> {
      // setUsers(response.data.response.users)
      console.log(response.data.response.flag);

      sessionStorage.setItem('lflag', JSON.stringify(response.data.response.flag));
      sessionStorage.setItem('user', JSON.stringify(response.data.response.user));
      sessionStorage.setItem('log', JSON.stringify(true));
      let lFlag = sessionStorage.getItem('lflag')
      lFlag = JSON.parse(lFlag)

      if(lFlag == 'true') {
        console.log('true');
        props.history.push("/admin/index");
      }
      else {
        console.log('false');
      }

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
  }



    return (
      <>
        <Col lg='5' md='7'>
          <Card className='bg-secondary shadow border-0'>
            <CardHeader className='bg-transparent pb-5'>
              <div className='text-muted text-center mt-2 mb-3'>
                <h3>Sign in</h3>
              </div>

            </CardHeader>
            <CardBody className='px-lg-5 py-lg-5'>

              <Form role='form'>
                <FormGroup className='mb-3'>
                  <InputGroup className='input-group-alternative'>
                    <InputGroupAddon addonType='prepend'>
                      <InputGroupText>
                        <i className='ni ni-email-83' />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input placeholder='Email' type='email' autoComplete='new-email' value={values.email} onChange={set('email')}/>
                  </InputGroup>
                </FormGroup>
                <FormGroup>
                  <InputGroup className='input-group-alternative'>
                    <InputGroupAddon addonType='prepend'>
                      <InputGroupText>
                        <i className='fas fa-keyboard' />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input placeholder='Password' type='password' autoComplete='new-password' value={values.password} onChange={set('password')}/>
                  </InputGroup>
                </FormGroup>
                <div className='custom-control custom-control-alternative custom-checkbox'>
                  <input
                    className='custom-control-input'
                    id=' customCheckLogin'
                    type='checkbox'
                  />
                  <label
                    className='custom-control-label'
                    htmlFor=' customCheckLogin'
                  >
                    <span className='text-muted'>Remember me</span>
                  </label>
                </div>
                <div className='text-center'>
                  <Button className='my-4' color='primary' type='button' onClick={() => Abc()} >
                    Sign in
                  </Button>
                </div>
              </Form>
            </CardBody>
          </Card>
          <Row className='mt-3'>
            <Col xs='6'>
              <a
                className='text-light'
                href='#pablo'
                onClick={e => e.preventDefault()}
              >
                <small>Forgot password?</small>
              </a>
            </Col>
            <Col className='text-right' xs='6'>
              <a
                className='text-light'
                href='/auth/register'
              >
                <small>Create new account</small>
              </a>
            </Col>
          </Row>
        </Col>
      </>
    );
  }

export default Login;
