import React, { useEffect, useState } from 'react';
import axios from 'axios';
import history from '../../../history.js';
import { Col, Row, Button, Form, FormGroup, Label, Input, Card, CardBody, CardSubtitle } from 'reactstrap';

import Header from '../../Headers/Header.js';

import { IconContext } from "react-icons";
import { FiCheckCircle } from "react-icons/fi";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const CUpdate = (props) => {
  const [values, setValues] = useState('');
  const [certificates, setCertificates] = useState([])
  const [alertMessage, setAlertMessage] = useState("")
  const [open, setOpen] = useState(false);
  const classes = useStyles();
  const handleClose = () => {
    setOpen(false);
  };
  const handleToggle = () => {
    setOpen(!open);
  };

    let CertID = sessionStorage.getItem('certID')
    CertID = JSON.parse(CertID)

    let NCert = sessionStorage.getItem('certificate')
    NCert = JSON.parse(NCert)

    window.onload = function() {
    	if(!window.location.hash) {
    		window.location = window.location + '#loaded';
    		window.location.reload();
    	}
    }

  useEffect(() => {
          if(!getData())
          {
            getData();
          }
  }, [])

  const token = 'YWRtaW5hZG1pbjEyMw==';

  const config = {
      headers: { Authorization: `Bearer ${token}` },
  };

  const bodyParameters = {
     key: "value"
  };

  const data = {
     id: CertID
  };

  const getData = () => {
    axios.post(
      'https://businessbook.co.in/api/api/govregi/single_read.php',
      data,
      config,
    ).then((response)=> {
      setCertificates(response.data.response.govregi)
      sessionStorage.setItem('certificate', JSON.stringify(response.data.response.govregi));
      console.log(NCert);
      setValues({
        id: CertID, govregi: NCert.govregi, timestamp: NCert.timestamp,
      });
    }).catch();
  }



  const Certificate = certificates;

  const set = name => {
    return ({ target: { value } }) => {
      setValues(oldValues => ({...oldValues, [name]: value }));
    }
  };

  const UpdateItem = () => {

    axios.put(
      'https://businessbook.co.in/api/api/govregi/',
      values,
      config,
    ).then((response)=> {
      // setCertificates(response.data.response.certificates)
      console.log(response);
      setAlertMessage(response.data.response.message);
      handleToggle();

      // setCState(response.data.response.state)
      // console.log(dist);
    }).catch();
    console.log(values);
    props.history.push('/admin/certificate')
  }

  return (
    <div>
      <Header />

    <Form style={{margin: '70px'}}>
      <FormGroup>
        <Label for="exampleID">ID</Label>
        <Input type="text" name="id" id="exampleID" value={Certificate.id} disabled />
      </FormGroup>
        <Row form>
          <Col md={6}>
            <FormGroup>
              <Label for="exampleName">Name</Label>
              <Input type="text" name="name" id="exampleName" defaultValue={Certificate.govregi} onChange={set('govregi')} />
            </FormGroup>
          </Col>
        </Row>
        <FormGroup>
          <Label for="exampleNumber">Timestamp</Label>
          <Input type="tel" name="number" id="exampleNumber" defaultValue={Certificate.timestamp} onChange={set('timestamp')} />
        </FormGroup>

      <Button className='btn-warning bcentr' onClick={() => UpdateItem()}>Update</Button>
    </Form>

    <Backdrop className={classes.backdrop} open={open}>
      <Card className='bcar'>
      <IconContext.Provider value={{ color: "#fb6340", size: '10rem' }}>
        <div className='bcar-body'><FiCheckCircle /></div>
      </IconContext.Provider>
        <CardSubtitle tag="h2" className='bcar-usub'>{alertMessage}</CardSubtitle>
        <CardBody  className='bcar-body'>
          <Button className='btn-warning'  onClick={handleClose} href='/admin/Certificate'>Done</Button>
        </CardBody>
      </Card>
    </Backdrop>
  </div>
  );
}

export default CUpdate;
